<template>
	<div class="diy_edit page_course_selection_information" id="course_selection_information_edit">
		<div class='warp'>
			<div class='container'>
				<div class='row diy_edit_content_box'>
						<div v-if="$check_field('set','course_name') || $check_field('add','course_name') || $check_field('get','course_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								课程名称:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_course_name" v-model="form['course_name']" placeholder="请输入课程名称" v-if="(form['course_name'] && $check_field('set','course_name')) || (!form['course_name'] && $check_field('add','course_name'))"  :disabled="disabledObj['course_name_isDisabled']"/>
							<span v-else-if="$check_field('get','course_name')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','lecturer') || $check_field('add','lecturer') || $check_field('get','lecturer')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								授课教师:
							</span>
						</div>
						<div class="diy_field diy_down">
							<select id="form_lecturer" :disabled="disabledObj['lecturer_isDisabled']" v-model="form['lecturer']" v-if="(form['lecturer'] && $check_field('set','lecturer')) || (!form['lecturer'] && $check_field('add','lecturer'))" >
								<option v-for="o in list_user_lecturer" :value="o['user_id']">
									{{o['nickname'] + '-' + o['username']}}
								</option>
							</select>
							<span v-else-if="$check_field('get','lecturer')">{{ form['lecturer'] }}</span>
						</div>
					</div>
							<div v-if="$check_field('set','student_users') || $check_field('add','student_users') || $check_field('get','student_users')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								学生用户:
							</span>
						</div>
						<div class="diy_field diy_down">
							<select id="form_student_users" :disabled="disabledObj['student_users_isDisabled']" v-model="form['student_users']" v-if="(form['student_users'] && $check_field('set','student_users')) || (!form['student_users'] && $check_field('add','student_users'))" >
								<option v-for="o in list_user_student_users" :value="o['user_id']">
									{{o['nickname'] + '-' + o['username']}}
								</option>
							</select>
							<span v-else-if="$check_field('get','student_users')">{{ form['student_users'] }}</span>
						</div>
					</div>
							<div v-if="$check_field('set','student_name') || $check_field('add','student_name') || $check_field('get','student_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								学生姓名:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_student_name" v-model="form['student_name']" placeholder="请输入学生姓名" v-if="(form['student_name'] && $check_field('set','student_name')) || (!form['student_name'] && $check_field('add','student_name'))"  :disabled="disabledObj['student_name_isDisabled']"/>
							<span v-else-if="$check_field('get','student_name')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','number_of_participants') || $check_field('add','number_of_participants') || $check_field('get','number_of_participants')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								选课人数:
							</span>
						</div>
								<!-- 选项 -->
						<div class="diy_field diy_down">
							<select id="form_number_of_participants" v-model="form['number_of_participants']" v-if="(form['number_of_participants'] && $check_field('set','number_of_participants')) || (!form['number_of_participants'] && $check_field('add','number_of_participants'))" >
								<option v-for="o in list_number_of_participants" :value="o">
									{{o}}
								</option>
							</select>
							<span v-else-if="$check_field('get','number_of_participants')">{{ form['number_of_participants'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','remarks') || $check_field('add','remarks') || $check_field('get','remarks')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								备注信息:
							</span>
						</div>
								<!-- 多文本 -->
						<div class="diy_field diy_desc">
							<textarea id="form_remarks" v-model="form['remarks']" v-if="(form['remarks'] && $check_field('set','remarks')) || (!form['remarks'] && $check_field('add','remarks'))" :disabled="disabledObj['remarks_isDisabled']" />
							<span v-else-if="$check_field('get','remarks')">{{ form['${obj.name}'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','course_no') || $check_field('add','course_no') || $check_field('get','course_no')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								选课编号:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_course_no" v-model="form['course_no']" placeholder="请输入选课编号" v-if="(form['course_no'] && $check_field('set','course_no')) || (!form['course_no'] && $check_field('add','course_no'))" :disabled="true"/>
							<span v-else-if="$check_field('get','course_no')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
	




				</div>
				<div class="diy_edit_submit_box row">
					<div class="col-12">
						<div class="btn_box">
							<button class="btn_submit" @click="submit()">提交</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import mixin from "@/mixins/page.js";
	export default {
		mixins: [mixin],
		components: {},
		data() {
			return {
				url_get_obj: "~/api/course_selection_information/get_obj?",
				url_add: "~/api/course_selection_information/add?",
				url_set: "~/api/course_selection_information/set?",

				// 登录权限
				oauth: {
					"signIn": true,
					"user_group": []
				},

				// 查询条件
				query: {
						"course_name": "",
							"lecturer": 0,
							"student_users": 0,
							"student_name": "",
							"number_of_participants": "",
							"remarks": "",
							"course_no": "",
						"course_selection_information_id": 0,
				},

				obj: {
						"course_name":  '', // 课程名称
							"lecturer": 0, // 授课教师
							"student_users": 0, // 学生用户
							"student_name":  '', // 学生姓名
							"number_of_participants":  '', // 选课人数
							"remarks":  '', // 备注信息
							"course_no": this.$get_stamp(), // 选课编号
						"course_selection_information_id": 0,
				},

				// 表单字段
				form: {
						"course_name":  '', // 课程名称
							"lecturer": 0, // 授课教师
							"student_users": 0, // 学生用户
							"student_name":  '', // 学生姓名
							"number_of_participants":  '', // 选课人数
							"remarks":  '', // 备注信息
							"course_no": this.$get_stamp(), // 选课编号
						"course_selection_information_id": 0,
				},
				disabledObj:{
						"course_name_isDisabled": false,
							"lecturer_isDisabled": false,
							"student_users_isDisabled": false,
							"student_name_isDisabled": false,
							"number_of_participants_isDisabled": false,
							"remarks_isDisabled": false,
							"course_no_isDisabled": false,
					},

								// 用户列表
				list_user_lecturer: [],
							// 用户列表
				list_user_student_users: [],
								// 选课人数选项列表
				list_number_of_participants: ['1'],
						
				// ID字段
				field: "course_selection_information_id",

			}
		},
		methods: {
			
						/**
			 * 获取老师用户用户列表
			 */
			async get_list_user_lecturer() {
				// if(this.user_group !== "管理员" && this.form["lecturer"] === 0) {
				//     this.form["lecturer"] = this.user.user_id;
				// }
				var json = await this.$get("~/api/user/get_list?user_group=老师用户");
				if(json.result && json.result.list){
					this.list_user_lecturer = json.result.list;
				}
				else if(json.error){
					console.error(json.error);
				}
			},
		
						/**
			 * 获取学生用户用户列表
			 */
			async get_list_user_student_users() {
				// if(this.user_group !== "管理员" && this.form["student_users"] === 0) {
				//     this.form["student_users"] = this.user.user_id;
				// }
				var json = await this.$get("~/api/user/get_list?user_group=学生用户");
				if(json.result && json.result.list){
					this.list_user_student_users = json.result.list;
				}
				else if(json.error){
					console.error(json.error);
				}
			},
					async get_user_session_student_users(){
				var _this = this;
				var json = await this.$get("~/api/user_group/get_obj?name=学生用户");
				if(json.result && json.result.obj){
					var source_table = json.result.obj.source_table;
					var user_id = _this.$store.state.user.user_id;
					if (user_id){
						var url = "~/api/"+source_table+"/get_obj?"
						this.$get(url, {"user_id":_this.$store.state.user.user_id}, function(res) {
							if (res.result && res.result.obj) {
								var arr = []
								for (let key in res.result.obj) {
									arr.push(key)
								}
								var arrForm = []
								for (let key in _this.form) {
									arrForm.push(key)
								}
								_this.form["student_users"] = user_id
								_this.disabledObj['student_users' + '_isDisabled'] = true
								for (var i=0;i<arr.length;i++){
                  if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
                    for (var j = 0; j < arrForm.length; j++) {
                      if (arr[i] === arrForm[j]) {
                        if (arr[i] !== "student_users") {
                          _this.form[arrForm[j]] = res.result.obj[arr[i]]
                          _this.disabledObj[arrForm[j] + '_isDisabled'] = true
                          break;
                        }
                      }
                    }
                  }
								}
							}
						});
					}
				}
				else if(json.error){
					console.error(json.error);
				}
			},
	
				
				
				
				
	
			/**
			 * 修改文件
			 * @param { Object } files 上传文件对象
			 * @param { String } str 表单的属性名
			 */
			change_file(files, str) {
				var form = new FormData();
				form.append("file", files[0]);
				this.$post("~/api/course_selection_information/upload?", form, (res) => {
					if (res.result) {
						this.form[str] = res.result.url;
					} else if (res.error) {
						this.$toast(res.error.message);
					}
				});
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_before(param){
				var form = $.db.get("form");
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
        //   this.obj = $.push(this.obj ,form);
				// 	this.form = $.push(this.form ,form);
				// }
				// var arr = []
				// for (let key in form) {
				// 	arr.push(key)
				// }
				// for (var i=0;i<arr.length;i++){
				// 	this.disabledObj[arr[i] + '_isDisabled'] = true
				// }
        if (form) {
          var arr = []
          for (let key in form) {
            arr.push(key)
          }
          var arrForm = []
          for (let key in this.form) {
            arrForm.push(key)
          }
          for (var i=0;i<arr.length;i++){
            if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
              for (var j = 0; j < arrForm.length; j++) {
                if (arrForm[j] === arr[i]) {
                  this.form[arrForm[j]] = form[arr[i]]
                  this.obj[arrForm[j]] = form[arr[i]]
                  this.disabledObj[arrForm[j] + '_isDisabled'] = true
                  break;
                }
              }
            }
          }
        }
														
        $.db.del("form");
				return param;
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_after(json ,func){
				// var form = $.db.get("form");
				// var obj = Object.assign({} ,form ,this.obj);
				// if (obj) {
        //   delete(obj.examine_state)
        //   delete(obj.examine_reply)
				// 	this.obj = $.push(this.obj ,obj);
				// }
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
				// 	this.form = $.push(this.form ,form);
				// }
				if(func){
					func(json);
				}
			},

		},
		created() {
									this.get_list_user_lecturer();
						this.get_user_session_student_users();
					this.get_list_user_student_users();
															},
	}
</script>

